
.container{
  margin: 0 250px 0  250px;
}


.group>*{
	border-radius:28px;
	border:2px solid #0e0f0f;
	display:inline-block;
	cursor:pointer;
	color:#0f0505d7;
	font-family:Arial;
	font-size:19px;
	padding:10px 36px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2f6627;
  margin: 10px;
  display: inline-block;
}
.delete,.add{
   background-color:#f16a52;
	border-radius:28px;
	border:2px solid #f50707;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:19px;
	padding:10px 36px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2f6627;
  margin: 10px;
}
.cancel{
   background-color:#f3ef1a;
	border-radius:28px;
	border:2px solid #f7f304;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:19px;
	padding:10px 36px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2f6627;
  margin: 10px;
}
.error {
      border-color: #E84444;
      -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(232,68,68,.6);
              box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(232,68,68,.6);
}
.navigation >*,.edit {
	background-color:#44c767;
	border-radius:28px;
	border:2px solid #18ab29;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:19px;
	padding:10px 36px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2f6627;
  margin: 10px;
}
.navigation>*:active,.navigation>*:focus,.delete:focus,.group>*:focus,.edit:focus,.cancel:focus{
  outline: none;
  position:relative;
	top:1px;
  -webkit-box-decoration-break: none;
          box-decoration-break: none;
}
.table{
  width: 100%;
  border:#2f6627 solid;
  border-radius: 15px
}
.table>*{
 	font-family:Arial;
	font-size:19px;
}
.table td{
 text-align: center;
}
.genre>*{
  display: inline;
}


























































